.c-tags {
    overflow: hidden;

    @include breakpoint(phone) {
        margin-top: 20px;
    }
}

.c-tags__item {
    display: block;
    float: left;
    margin: 5px 10px 5px 0;
    background: $color-2;
    color: $color-moon;
    border-radius: 5px;
    padding: 2px 10px;
    font-size: $font-size-small;
}
