.c-contact-form {
	@extend %clearfix;

	.c-button {
		float: right;
		margin-top: 15px;
	}
}

.c-contact-form__field {
    z-index: 5;
}

.c-contact-form__error {
    position: absolute;
    top: -15px;
    right: 0;
    background: $color-3;
    color: $color-white;
    padding: 0 10px;
    height: 32px;
    line-height: 32px;

    &:after {
        content: "";
        display: block;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-top: 10px solid $color-3;
        position: absolute;
        top: 32px;
        right: 20px;
    }
}

@include breakpoint(tablet) {
    .c-contact-form__group {
        &:first-child {
            @include span(6);
        }

        &:last-child {
            @include span(6 at 7);
        }
    }
}
